<template>
  <v-container ref="boxIds" class="px-0 welcome-box">
    <v-row class="webox-wraper" gutters="20">
      <v-col v-if="isTourist" cols="12">
        <div
          class="weitem_bg weitem_main"
          :style="{
            backgroundImage: `url(${boxBgImage[0]})`
          }"
        >
          <div class="weitem_main_title">
            <auto-font
              :level="1"
              font-weight="600"
              :maxFont="loadVariables(isApp ? 'weitem_main_title_fs_xs' : 'weitem_main_title_font_size')"
              textAlign="left"
              :text="getMoreLangText('guidance_hall_register_title')"
            />
          </div>
          <div class="weitem_main_content">
            <ParagraphFit :text="getMoreLangText('guidance_hall_register_contents')" :maxFont="maxContentFontSize" />
          </div>
          <div class="weitem_main_action">
            <v-btn class="weitem_btn" color="t_primary_dark_color" @click="onSignUp()">
              <span :style="{ fontSize: isApp ? '13px' : '20px' }">{{ $t('UI_Sign_Up') }}</span>
            </v-btn>
            <div v-if="isShowLogin.facebook" class="weitem_main_action_item" @click="thrSignIn('facebook')">
              <img src="@image/login/tr_facebook.png" />
            </div>
            <div v-if="isShowLogin.google" class="weitem_main_action_item" @click="thrSignIn('google')">
              <img src="@image/login/tr_google.png" />
            </div>
            <div v-if="isShowLogin.telegram" class="weitem_main_action_item" @click="thrSignIn('telegram')">
              <img src="@image/login/tr_telegram.png" />
            </div>
          </div>
          <div class="weitem_main_meta">
            <div class="weitem_main_meta_item">
              <icon-font name="icon-renyuan" dark="t_white" :size="isApp ? 12 : 18" />
              <span class="meta_text">{{ $t('UI_Sign_Up') }}</span>
              <icon-font class="icon-arrow" name="icon-Icon_Right" :size="isApp ? 12 : 18" />
            </div>
            <div class="weitem_main_meta_item">
              <icon-font name="icon-Icon_Deposlt" dark="t_white" :size="isApp ? 12 : 18" />
              <span class="meta_text">{{ $t('UI_VIP_Vip.deposit') }}</span>
              <icon-font class="icon-arrow" name="icon-Icon_Right" :size="isApp ? 12 : 18" />
            </div>
            <div class="weitem_main_meta_item">
              <icon-font class="icon_meta" name="icon-chouma" dark="t_white" :size="isApp ? 12 : 18" />
              <span class="meta_text">{{ $t('UI_GETBONUS') }}</span>
            </div>
          </div>
        </div>
      </v-col>
      <v-col v-else cols="12">
        <div
          class="weitem_bg weitem_main"
          :style="{
            backgroundImage: `url(${boxBgImage[0]})`
          }"
        >
          <div class="weitem_main_title">
            <auto-font
              :level="1"
              font-weight="600"
              :maxFont="loadVariables(isApp ? 'weitem_main_title_fs_xs' : 'weitem_main_title_font_size')"
              textAlign="left"
              :text="getMoreLangText('guidance_hall_register_title')"
            />
          </div>
          <vip-box class="vip-box" />
        </div>
      </v-col>
      <v-col cols="12" sm="6">
        <div
          class="weitem_bg weitem_submain"
          :style="{
            backgroundImage: `url(${boxBgImage[1]})`
          }"
        >
          <div class="weitem_submain_title">{{ menuText }}</div>
          <div class="weitem_submain_content">
            <ParagraphFit :text="getMoreLangText('guidance_hall_jump_contents')" :maxFont="maxContentFontSize" />
          </div>
          <v-btn class="weitem_submain_btn" color="t_primary_dark_color" @click="gotoCasinoView()">
            <span :style="{ fontSize: isApp ? '13px' : '20px' }">{{ btnGotoText }}</span>
          </v-btn>
        </div>
      </v-col>
      <v-col cols="12" sm="6">
        <div
          class="weitem_bg weitem_submain"
          :style="{
            backgroundImage: `url(${boxBgImage[2]})`
          }"
        >
          <div class="weitem_submain_title">
            {{ $t('UI_Tab_Sports.sports') }}
          </div>
          <div class="weitem_submain_content">
            <ParagraphFit :text="getMoreLangText('guidance_hall_sport_jump_contents')" :maxFont="maxContentFontSize" />
          </div>
          <v-btn class="weitem_submain_btn" color="t_primary_dark_color" @click="gotoSportView()">
            <span :style="{ fontSize: isApp ? '13px' : '20px' }">{{ $t('UI_GotoSports') }}</span>
          </v-btn>
        </div>
      </v-col>
    </v-row>
    <thr-login ref="thrLogin" :isLogin="false" @loginRegisterSuccess="signInSuccess" />
  </v-container>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { loadWebsocket } from '@/server/ws.js'
import { AdjustlogEvent } from '@/utils/helper'
import ContainerMixin from '@/components/containerMixin'
import ThrLogin from '@/components/login/thr-login.vue'
import VipBox from './vip-box.vue'
import PublishSubscribe from 'publish-subscribe-js'
import variables from '@style/views/menu.welcome_hall.modules.scss'
import ParagraphFit from '@/components/ui/paragraph-fit'

import { ADS_TYPE, EVENT_KEY, dynamicInitAds, logEvent } from '@/server/ads/index'

export default {
  name: 'VisitView',
  components: {
    ParagraphFit,
    ThrLogin,
    VipBox
  },
  mixins: [ContainerMixin],
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig', 'langMoreInfo']),
    ...mapGetters('base', ['getMoreLangText', 'getShowConfig', 'isTourist']),
    // 控制登录选项
    isShowLogin() {
      const google = this.getShowConfig('google_login') == 1
      const facebook = this.getShowConfig('facebook_btn') == 1
      const telegram = this.getShowConfig('telegram_login') == 1
      return {
        google,
        facebook,
        telegram
      }
    },
    boxBgImage({ isApp, systemConfig }) {
      const { guidance_hall_register_img_pc, guidance_hall_register_img_mobile } = systemConfig
      if (isApp) {
        return [
          guidance_hall_register_img_mobile || require('@image/menus/we_reg_xs.png'),
          require('@image/menus/we_casino_xs.png'),
          require('@image/menus/we_sport_xs.png')
        ]
      } else {
        return [
          guidance_hall_register_img_pc || require('@image/menus/we_reg.png'),
          require('@image/menus/we_casino.png'),
          require('@image/menus/we_sport.png')
        ]
      }
    },
    maxContentFontSize() {
      return this.isApp ? variables.weitem_main_content_fs_xs : variables.weitem_main_content_fs
    },
    // is_hall_btn_name 0 显示 Casino 1  iLottery
    menuText() {
      return this.systemConfig.is_hall_btn_name == 1 ? this.$t('UI_Menu_AllGames') : this.$t('UI_Menu_Casino')
    },
    // is_hall_btn_name 0 显示 Casino 1  iLottery
    btnGotoText() {
      return this.systemConfig.is_hall_btn_name == 1 ? this.$t('UI_GotoiLottery') : this.$t('UI_GotoCasino')
    }
  },
  methods: {
    loadVariables(name) {
      return variables[name]
    },
    routeTo(routeName) {
      this.$router.push({ name: routeName })
    },
    onSignUp() {
      this.$store.dispatch('modal/setLoginRegistShow', { tab: false })
    },
    // 登录/注册成功后操作
    thrSignIn(signInType) {
      this.$refs.thrLogin.signIn(signInType)
    },

    // 登录打点
    handleLoginLog() {
      logEvent(EVENT_KEY.login)
    },
    gotoCasinoView() {
      // 记录首页引导跳转
      if (!this.isTourist) {
        const storageKey = 'first_guide_redirect_' + this.user.uid
        localStorage.setItem(storageKey, 'menu')
      }
      this.$emit('show-casino')
    },

    gotoSportView() {
      // 记录首页引导跳转
      if (!this.isTourist) {
        const storageKey = 'first_guide_redirect_' + this.user.uid
        localStorage.setItem(storageKey, 'sportView')
      }
      this.$router.push({ path: '/sportView' })
    },

    // 注册打点
    handleRegistrationLog() {
      AdjustlogEvent(this.systemConfig, 'register')
      logEvent(EVENT_KEY.registration)
      setTimeout(() => {
        this.handleLoginLog()
      }, 500)
    },

    async signInSuccess({ data = {}, isLogin = true }) {
      const { token = '', apk_info = { apk_send_gold: 0, apk_show_switch: 0 }, pixelid } = data
      this.$store.commit('base/SET_SHOW_RED_DOT', {})
      this.$store.commit('base/SET_USER', data)
      this.$store.commit('base/SET_USER_HISTORY', data)
      loadWebsocket(this)
      // 获取下载金币信息 / 壳包下载 活动充值获取金币
      this.$store.commit('base/SET_APK_ACTIVITY_GOLD', apk_info)
      // aj 打点
      this.$store.dispatch('base/getAjNoticeFun')
      // 打点
      const logHandler = isLogin ? this.handleLoginLog : this.handleRegistrationLog
      logHandler()
      // 同步本地 facebooke ads 缓存
      if (isLogin && pixelid) dynamicInitAds(ADS_TYPE.facebook)
      // 请求防沉迷配置接口
      this.$store.dispatch('responsible/getFeatureInfo', {
        token,
        language: this.languageType,
        callback: () => {
          // 防沉迷更新提醒功能倒计时本地缓存数据
          this.$store.dispatch('responsible/renewRemindStorage', {
            sign: 'login',
            data
          })
        }
      })

      // 请求红点提示
      await this.$store
        .dispatch('base/showRedDotReceivedFun', {
          version: 1,
          token,
          language: this.languageType
        })
        .catch(e => e)
      // 邀请活动登录需要获取新的信息
      await this.$store
        .dispatch('source/getActivityConfigFn', {
          token,
          language: this.languageType
        })
        .catch(e => e)
      // 获取回归任务弹窗数据
      await this.$store
        .dispatch('base/getRegressionDialog', {
          token,
          language: this.languageType
        })
        .catch(e => e)

      // 清除弹窗，根据页面重新初始化
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_CLEAR)
      this.$nextTick(() => {
        PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, this.$route.name)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/menu.welcome_hall.modules.scss';
.weitem_bg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 20px;
}
.weitem_main {
  width: 100%;
  height: $weitem_main_height;
  padding: $weitem_main_pa;
}
.weitem_main_title {
  width: 72%;
  height: $weitem_main_title_height;
  margin-bottom: $weitem_main_title_mb;
  overflow: hidden;
}
.weitem_main_content {
  width: 62%;
  height: $weitem_main_content_height;
  font-size: $weitem_main_content_fs;
  overflow: hidden;
}
.weitem_main_action {
  display: flex;
  align-items: center;
  margin-top: $weitem_main_action_mt;
  gap: 20px;
  .weitem_btn {
    width: 200px;
    height: 64px;
    border-radius: 16px;
  }
}

.weitem_main_meta {
  display: flex;
  margin-top: $weitem_main_meta_mt;
  gap: $weitem_main_meta_gap;
}

.weitem_main_meta_item {
  display: flex;
  align-items: center;
  .meta_text {
    color: $colorText_meta_item;
    margin-left: $weitem_main_meta_item_meta_text_ml;
  }
  .icon-arrow {
    margin-left: $weitem_main_meta_item_icon-arrow_ml;
  }
}

.weitem_main_action_item {
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: transform 0.2s;
  &:hover {
    transform: scale(1.05);
  }
}

.weitem_submain {
  width: 100%;
  height: $weitem_submain_height;
  padding: $weitem_submain_height_pa;
  .weitem_submain_btn {
    margin-top: $weitem_submain_btn_mt;
    width: $weitem_submain_btn_width;
    height: $weitem_submain_btn_height;
    border-radius: $weitem_submain_btn_radius;
    ::v-deep {
      .v-btn__content {
        text-transform: unset !important;
      }
    }
  }
}

.weitem_submain_title {
  font-size: $weitem_submain_title;
  font-weight: 600;
}
.weitem_submain_content {
  width: $weitem_submain_content_width;
  font-size: $weitem_submain_content_fs;
  font-weight: 600;
  color: $colorText_meta_item;
  height: $weitem_submain_content_height;
  overflow: hidden;
}

.vip-box {
  width: 56%;
}

@include breakpoint-only-xs() {
  .weitem_bg {
    border-radius: $weitem_bg_radius_xs;
  }
  .weitem_main {
    height: $weitem_main_height_xs;
    padding: $weitem_main_pa_xs;
  }
  .weitem_main_title {
    height: $weitem_main_title_height_xs;
    margin-top: $weitem_main_title_mb_xs;
  }
  .weitem_main_content {
    font-size: $weitem_main_content_fs_xs;
    height: $weitem_main_content_height_xs;
    width: 60%;
  }
  .weitem_main_action {
    gap: 10px;
    margin-top: $weitem_main_action_xt;
    .weitem_btn {
      width: $weitem_btn_width_xs;
      height: $weitem_btn_height_xs;
      border-radius: $weitem_btn_radius_xs;
    }
  }

  .weitem_main_meta {
    gap: $weitem_main_meta_xs;
  }

  .weitem_main_action_item > img {
    width: $weitem_main_action_item_img_width_xs;
  }
  .weitem_main_meta_item {
    .meta_text {
      font-size: $weitem_main_meta_item_meta_text_fs_xs;
      margin-left: $weitem_main_meta_item_meta_text_ml_xs;
    }
    .icon-arrow {
      margin-left: $weitem_main_meta_item_icon-arrow_ml_xs;
    }
  }
  .weitem_submain {
    height: $weitem_submain_height_xs;
    padding: $weitem_submain_pa_xs;
    .weitem_submain_btn {
      margin-top: $weitem_submain_btn_mt_xs;
      width: $weitem_submain_btn_width_xs;
      height: $weitem_submain_btn_height_xs;
      border-radius: $weitem_submain_btn_radius_xs;
    }
  }
  .weitem_submain_title {
    font-size: $weitem_submain_title_fs_xs;
  }
  .weitem_submain_content {
    font-size: $weitem_submain_content_fs_xs;
    width: 62%;
    height: $weitem_submain_content_height_xs;
  }

  .vip-box {
    width: 68%;
  }
}
</style>
