<template>
  <div class="comp game_recent" v-if="gameList?.length">
    <div class="recent_title">
      <img class="recent_title_img" :src="loadIcon('recent_logo')" alt="" />
      <span class="recent_title_text">{{ $t('UI.Recent') }}</span>
    </div>
    <div class="recent_ul">
      <div
        class="recent_li"
        v-for="(item, i) in gameList"
        :key="item.gid_gameid"
        :style="`margin: ${i != 0 ? cardSize.margin : ''}`"
      >
        <game-card
          ref="gameCardRef"
          :is-collect="false"
          :game="item"
          :width="cardSize.width"
          :height="cardSize.height"
          scene="recent"
          @toGame="toGame"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import loadIcon from '@/assets/icons'
import variables from '@style/views/menu.index.modules.scss'
import GameCard from './game-card'

export default {
  props: {
    list: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  components: {
    GameCard
  },
  data() {
    return {
      gameList: []
    }
  },
  watch: {
    list: {
      deep: true,
      immediate: true, //立即监听
      handler(e) {
        let list = JSON.parse(JSON.stringify(e))
        list = list.map(item => {
          return {
            ...item,
            gid_gameid: item.has_child == 0 ? item.id + '_' + item.gid : item.id + '_' + item.gid + '_' + item.gameid
          }
        })
        // 游戏列表，app取前5 & 过滤掉禁用的
        this.gameList = list.filter(v => !v.disabled).slice(0, this.isApp ? 5 : 8)
      }
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('source', ['recent']),
    ...mapState('layout', ['containerBox']),
    // 游戏卡片 width/height/margin
    cardSize() {
      const {
        lobby_box_padded,
        xs_lobby_box_padded,
        game_recent_indiv_app,
        game_recent_indiv_pc,
        game_recent_spacing_app,
        game_recent_spacing_pc
      } = variables
      // 分配个数
      const indiv = this.isApp ? game_recent_indiv_app : game_recent_indiv_pc
      // 间距
      const spacing = this.isApp ? game_recent_spacing_app : game_recent_spacing_pc

      const outer_padded = this.isApp ? xs_lobby_box_padded * 2 : lobby_box_padded * 2
      const fullWidth = this.containerBox.width - outer_padded
      const width = `${(fullWidth - (indiv - 1) * spacing) / indiv}px`

      return {
        width,
        height: width,
        margin: `0 0 0 ${spacing}px`
      }
    }
  },
  methods: {
    loadIcon: type => loadIcon(type, false),
    /**
     * 跳转游戏
     * @param {*} game 游戏信息
     */
    toGame(game) {
      this.$emit('toGame', game)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/menu.index.modules.scss';

// 针对移动端样式
.comp.game_recent {
  width: $game_recent_width;
  margin: $game_recent_margin;
  @include setBox(column, center, flex-start);

  .recent_title {
    width: $game_recent_title_width;
    margin: $game_recent_title_margin;
    line-height: $game_recent_title_line;
    @include setBox(row, flex-start, center);

    .recent_title_img {
      width: $game_recent_title_img_width;
      margin: $game_recent_title_img_margin;
    }

    .recent_title_text {
      font-size: $game_recent_title_text_size;
      font-weight: $game_recent_title_text_weight;
      color: $game_recent_title_text_color;
    }
  }

  .recent_ul {
    width: $game_recent_ul_width;
    @include setBox(row, flex-start, center);
  }
}

// 针对pc样式
@include breakpoint-only-up() {
  .comp.game_recent {
    margin: $game_recent_pc_margin;
  }
}
</style>
