<template>
  <div v-if="play" class="comp horse_race" :style="horse_race_bg">
    <div id="horseNoticeRtId" class="horse_race_box">
      <div id="horseNoticeContentId" class="content" v-html="playItem.text"></div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getBroadcastListCache } from '@/api/modules/config'
import PublishSubscribe from 'publish-subscribe-js'
import menuType from '@/events/menu'
import { getEeplace } from '@/utils/helper.js'
import loadIcon from '@/assets/icons'

export default {
  data() {
    return {
      playIndex: 0, // 固定广播 播放索引
      rate: 30, // 速率 = 40px/s
      regularList: [
        // 接口获取的固定广播列表
        // {
        //     id: ''
        //     sign: 'regular',    // 标识
        //     text: '',           // 文案
        //     time: 0             // 时间
        // }
      ],
      broadcasList: [
        // 推送的广播列表
        // {
        //     id: ''
        //     sign: 'broadcas',   // 标识
        //     text: '',           // 文案
        //     time: 0             // 时间
        // }
      ],
      playItem: {}, // 当前播放的广播
      play: false // 开始, 结束播放
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'languageType', 'user']),
    // 背景
    horse_race_bg() {
      return {
        background: `url(${loadIcon('horse_race_bg', false)})`,
        backgroundSize: '100% 100%',
        backgroundRepeat: 'no-repeat'
      }
    }
  },
  mounted() {
    this.getBroadcast()

    // 接收广播
    PublishSubscribe.subscribe(menuType.MENU_BROADCAST, this.wsBroadcast)
  },
  methods: {
    loadIcon: k => loadIcon(k, false),
    // 接收推送的广播
    wsBroadcast(text = '') {
      const item = { id: new Date().getTime(), sign: 'broadcas', text: getEeplace(text), time: 0 }

      this.broadcasList.push(item)
      this.playInit('broadcas')
    },
    // 获取广播列表
    async getBroadcast() {
      const params = {
        language: this.languageType,
        token: this.user.token
      }
      const res = await getBroadcastListCache(params).catch(e => e)
      if (!res || res.code != 0) return

      const resData = res.data && Array.isArray(res.data) ? res.data : []
      this.regularList = resData.map((text, i) => {
        return {
          id: new Date().getTime(),
          sign: 'regular',
          text: text.content,
          time: text.interval
        }
      })

      // 初始化的时候，默认播放第一条广播
      const firstItem = this.regularList[this.playIndex]
      if (firstItem) this.playInit('regular')
    },
    /**
     * 开始播放
     * @param {*} sign regular:固定广播 broadcas:推送广播
     */
    playInit(sign = 'regular') {
      if (this.play) return

      this.playItem = sign == 'regular' ? this.regularList[this.playIndex] : this.broadcasList[0]

      if (!this.playItem || Object.keys(this.playItem).length <= 0) return

      // 开始播放
      this.play = true

      this.$nextTick(() => {
        // 播放限制区域
        const horseNoticeRtId = document.getElementById('horseNoticeRtId')
        if (horseNoticeRtId) {
          // 获取到播放限制区域的宽度
          const horseNoticeRt_w = horseNoticeRtId.clientWidth || horseNoticeRtId.offsetWidth

          // 获取到要播放内容的区域，插入动画
          const horseNoticeContentId = document.getElementById('horseNoticeContentId')
          horseNoticeContentId.removeEventListener('animationend', this.playEnd)
          horseNoticeContentId.style['animationName'] = ''

          if (horseNoticeContentId) {
            // 获取到要播放内容区域的宽度
            const horseNoticeContent_w = horseNoticeContentId.clientWidth || horseNoticeContentId.offsetWidth
            // 播放时间 + 【额外增加 已过和未过 的边距】
            const time =
              ((horseNoticeContent_w <= horseNoticeRt_w ? horseNoticeRt_w : horseNoticeContent_w) + 60) / this.rate
            // 插入动画
            horseNoticeContentId.style['animationName'] = 'horse-notice'
            horseNoticeContentId.style['animationDuration'] = `${time}s`
            horseNoticeContentId.style['animationTimingFunction'] = 'linear'
            horseNoticeContentId.style['animationIterationCount'] = '1'
            horseNoticeContentId.style['animationFillMode'] = 'both'

            const rules = `
                @keyframes horse-notice {
                    0% {
                        right: -${horseNoticeRt_w + 30}px;
                    }

                    100% {
                        right: ${horseNoticeContent_w + 30}px;
                    }
                }
              `
            const nameRule = this.getKeyframes('horse-notice')
            if (JSON.stringify(nameRule) == '{}') {
              let sheet = document.styleSheets[0]
              sheet.insertRule(rules, 0)
            } else {
              nameRule.styleSheet.deleteRule(nameRule.index)
              nameRule.styleSheet.insertRule(rules, nameRule.index)
            }

            // 监听动画是否结束
            horseNoticeContentId.addEventListener('animationend', this.playEnd)
          }
        }
      })
    },
    // 结束播放
    playEnd() {
      // 结束播放
      this.play = false

      // 判断当前播放的是推送广播还是固定广播 regular:固定广播 broadcas:推送广播
      const playSign = this.playItem && this.playItem.sign ? this.playItem.sign : 'regular'
      if (playSign == 'regular') {
        // 根据当前播放广播间隔时间倒计时显示下一个固定广播
        const delItem = this.regularList.find(f => f.id == this.playItem.id) || {}
        const time = delItem.time || 0

        // 固定系统广播倒计时
        this.playTime = setTimeout(() => {
          this.playIndex++
          const item = this.regularList[this.playIndex]
          if (item) {
            this.playInit('regular')
          } else {
            this.playIndex = 0
            this.playInit('regular')
          }

          // 清除固定广播倒计时
          clearTimeout(this.playTime)
          this.playTime = null
        }, time * 1000)

        // 判断有无推送广播。 ==> 有则立即执行
        if (this.broadcasList.length > 0) this.playInit('broadcas')
      } else {
        // 删除已经播放的推送的广播
        const i = this.broadcasList.findIndex(f => f.id == this.playItem.id)
        if (i != -1) this.broadcasList.splice(i, 1)

        // 判断有无推送广播。 ==> 有则立即执行，无则根据当前播放广播间隔时间倒计时显示下一个固定广播
        if (this.broadcasList.length > 0) {
          this.playInit('broadcas')
        } else {
          // 判断当前是否有固定广播的倒计时，没有则重新开始执行
          if (!this.playTime) {
            const regularItem = this.regularList[this.playIndex]

            if (regularItem) {
              this.playInit('regular')
            } else {
              this.playIndex = 0
              this.playInit('regular')
            }
          }
        }
      }
    },
    // 判断是否有 keyframes 动画
    getKeyframes(name) {
      const animation = {}
      const styleSheets = document.styleSheets
      for (let i = 0; i < styleSheets.length; i++) {
        const item = styleSheets[i]
        if (item.cssRules[0] && item.cssRules[0].name && item.cssRules[0].name == name) {
          animation.cssRule = item.cssRules[0]
          animation.styleSheet = item
          animation.index = 0
        }
      }
      return animation
    }
  },
  beforeDestroy() {
    if (this.playTime) {
      clearTimeout(this.playTime)
      this.playTime = null
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/menu.index.modules.scss';

.comp.horse_race {
  width: calc(100% - #{$horse_race_left} - #{$horse_race_right});
  position: $horse_race_position;
  left: $horse_race_left;
  right: $horse_race_right;
  z-index: 99;
  border-radius: $horse_race_radius;
  padding: $horse_race_padding;
  @include setBox(row, flex-start, center);

  .horse_race_box {
    width: 100%;
    overflow: hidden;

    .content {
      min-width: 100%;
      display: inline-block;
      position: relative;
      white-space: nowrap;
      word-break: normal;
    }
  }
}
</style>
