<template>
  <div class="game-control">
    <v-text-field
      v-model="keywords"
      class="search-field input_radius"
      background-color="t_input_light_bg"
      outlined
      :placeholder="$t('General.General_5')"
      :height="loadVariables('search_field_height')"
      hide-details
      single-line
      solo
      flat
      dense
      @keyup.enter="goPesquisa()"
    >
      <template slot="prepend-inner">
        <icon-font class="prepend-icon" name="icon-Icon_Search" :size="loadVariables('icon_search_size')" />
      </template>
    </v-text-field>
    <swiper id="category-swiper" ref="swiperRef" :options="swiperOptionsInfo">
      <swiper-slide
        v-for="(item, index) in list"
        :key="item.id"
        class="game-category-item"
        :class="[
          activeIndex === index && 'game-category-item--active',
          !typeNameFn(item) && 'game-category-item-min-width'
        ]"
        @click.native="change(index, $event)"
      >
        <img v-if="item.icon" :src="pictureUrl + item.icon" />
        <span>{{ typeNameFn(item) }}</span>
      </swiper-slide>
      <div class="swiper-scrollbar" :class="{ 'd-block': isApp }" slot="scrollbar"></div>
    </swiper>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import variables from '@style/views/menu.index.modules.scss'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

export default {
  name: 'GameControl',
  components: {
    Swiper,
    SwiperSlide
  },
  props: {
    list: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeIndex: 0,
      keywords: '',
      options: {}
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'languageType']),
    ...mapState('source', ['pictureUrl'])
  },
  watch: {
    isApp() {
      this.bindSwiperRect()
    }
  },
  created() {
    this.swiperOptionsInfo = {
      freeMode: true,
      freeModeMomentumRatio: 0.5,
      resistanceRatio: 0.1,
      slidesPerView: 'auto',
      spaceBetween: 12,
      scrollbar: {
        el: '.swiper-scrollbar',
        hide: true,
        draggable: true
      }
    }
  },
  mounted() {
    this.bindSwiperRect()
  },
  methods: {
    loadVariables(name) {
      return variables[name]
    },
    bindSwiperRect() {
      const swiperInstance = this.$refs.swiperRef.$swiper
      this.swiperInstance = swiperInstance
      this.swiperRect = swiperInstance.$el[0].getBoundingClientRect()
    },
    swipeScroll(index) {
      const { swiperInstance, swiperRect } = this
      const swiperWidth = swiperRect.width
      const slide = swiperInstance.slides[index]
      const slideLeft = slide.offsetLeft
      const slideWidth = slide.clientWidth
      const slideRight = slideLeft + slideWidth
      const slideRect = slide.getBoundingClientRect()
      swiperInstance.setTransition(300)
      // 视区左侧区域
      if (slideRect.left - swiperRect.left < 0) {
        swiperInstance.setTranslate(-slideLeft)
        return
      }
      // 视区右侧区域
      if (slideRect.right - swiperRect.right > 0) {
        swiperInstance.setTranslate(-(slideRight - swiperWidth))
        return
      }
    },
    change(index, e) {
      if (this.disabled) return
      if (this.activeIndex === index) {
        return
      }
      this.swipeScroll(index)
      this.activeIndex = index
      this.$emit('change', this.list[index])
    },
    goPesquisa() {
      this.$router.push({
        name: 'pesquisa',
        query: {
          keywords: this.keywords
        }
      })
    },
    // 英文是显示type_name_en 其他语言type_name
    typeNameFn(item) {
      return this.languageType == 'en-us' ? item.type_name_en : item.type_name
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/menu.index.modules.scss';

.game-control {
  margin-top: $game_control_mt;
  .search-field {
    font-size: $search_field_font_size;
    font-weight: $search_field_font_weight;
    .prepend-icon {
      margin-right: $search_field_prepend_icon_mr;
    }
  }
  ::v-deep #category-swiper {
    padding: $category_scrollbar_padding;
    margin-top: $category_scrollbar_mt;
    .game-category-item {
      display: flex;
      align-items: center;
      width: auto;
      background: $category-item-background;
      box-shadow: $category-item-boxshadow;
      border-radius: $category-item-radius;
      height: $category-item-height;
      padding: $category-item-padding;
      cursor: pointer;
      user-select: none;
      & > img {
        width: $category-img-width;
        height: $category-img-height;
      }
      & > span {
        margin-left: $category-name-margin-left;
        font-size: $category-name-font;
        font-weight: $category-name-font-weight;
        white-space: nowrap;
      }
    }
    .game-category-item--active {
      background: $category-item-active-background;
      box-shadow: $category-item-active-box-shadow;
      &:hover {
        @include setFilter($btn_hover_filter, $btn_hover_filter_type);
      }
    }
    .game-category-item-min-width {
      min-width: 80px;
    }
    .swiper-scrollbar {
      display: none;
      height: $category-scrollbar-height;
      background: $category_swiper_scrollbar_bg;
      left: 0;
      right: 0;
      bottom: 0;
    }
    .swiper-scrollbar-drag {
      background: $category_swiper_scrollbar_drag_bg;
    }
  }
}

@include breakpoint-only-xs() {
  .game-control {
    margin-top: $xs_game_control_mt;
    ::v-deep #category-swiper {
      padding: $xs_category_scrollbar_padding;
      margin-top: $xs_category_scrollbar_mt;
    }
  }
}
</style>
