<template>
  <div v-if="list.length" class="game-group-slider" :class="{ game_index: index == 0 && recentList.length <= 0 }">
    <div class="game-group-head">
      <div class="game-group-head--left">
        <img v-if="icon" class="game-group-ico" :src="icon" />
        <span class="game-group-name">{{ name }}</span>
      </div>
      <div class="game-group-head--action" @click="onGoFeatured">
        <span>{{ $t('UI_Load_more') }}</span>
        <icon-font name="icon-Icon_Right" :size="loadVariables('group_icon_size')" />
      </div>
    </div>
    <div class="game-group-wrapper" :class="{ 'game-group-wrapper--app': isApp }">
      <!-- <div ref="scroll" class="scroll-wrapper" :class="{'flex-wrap': !isApp}"> -->
      <div v-for="(item, index) in currentList" :key="item.keys" class="game-wrapper">
        <game-card
          :index="index"
          :game="item"
          :width="item.isFully ? cardSize.fullWidth : cardSize.width"
          :height="cardSize.height"
          @toGame="toGame"
        />
      </div>
      <!-- </div> -->
    </div>
    <div v-if="isShowMore" class="game-group-action">
      <button v-ripple class="btn--action" @click="handleNext">{{ $t('General.General_8') }}</button>
    </div>
    <slot></slot>
    <div
      v-if="bannerUrl"
      class="banner_wrap"
      :class="{ gap: !isShowMore || $slots.default }"
      :style="{ '--bannerUrl': `url(${bannerUrl})` }"
      @click="onClickBanner"
    >
      <div class="banner"></div>
    </div>
  </div>
</template>

<script>
import variables from '@style/views/menu.index.modules.scss'
import { mapState } from 'vuex'
import GameCard from './game-card'
import loadGameImg from '@/utils/loadGameImg'
import { jump } from '@/utils/linkMap'

const { game_wrapper_space, game_wrapper_app_space, lobby_box_padded, xs_lobby_box_padded } = variables

export default {
  name: 'GameGroup',
  components: {
    GameCard
  },
  props: {
    // 索引
    index: {
      type: Number,
      default: 0
    },
    icon: {
      type: String,
      default: ''
    },
    name: {
      type: String,
      default: ''
    },
    defaultSize: [Number],
    list: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Boolean,
      default: false
    },
    banner: {
      type: Object,
      default: () => null
    }
  },
  data() {
    return {
      page: 1,
      timer: null
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('layout', ['containerBox']),
    ...mapState('source', ['recent']),
    size() {
      // app 时不完全展示3个
      return this.isApp ? 3 : 6
    },
    recentList() {
      return this.recent.filter(v => !v.disabled) || []
    },
    cardSize() {
      // 原图大小 160 /220
      const size = this.size
      const spacing = this.isApp ? game_wrapper_app_space * 2 : game_wrapper_space * 2
      const outer_padded = this.isApp ? xs_lobby_box_padded * 2 : lobby_box_padded * 2
      const w = 160
      const h = 220
      const rate = h / w
      const fullWidth = this.containerBox.width - outer_padded
      const width = (fullWidth + spacing) / size - spacing
      return {
        fullWidth: `${fullWidth}px`,
        width: `${width}px`,
        height: `${width * rate}px`
      }
    },
    // 是否展示加载更多分页
    isShowMore() {
      return this.defaultSize * this.page >= this.list.length ? false : true
    },
    currentList() {
      if (Array.isArray(this.list)) {
        const endIndex = this.page * this.defaultSize
        return this.list.slice(0, endIndex)
      } else {
        return []
      }
    },
    bannerUrl() {
      if (this.banner) {
        return this.isApp ? this.banner.mobile_img_url : this.banner.pc_img_url
      }
      return ''
    }
  },
  watch: {
    isApp: {
      handler() {
        this.page = 1
      },
      immediate: true
    }
  },
  methods: {
    loadVariables(name) {
      return variables[name]
    },
    // type  大厅移动端更多信息为1
    toGame(game, type) {
      if (this.disabled) return
      if (type == 1) {
        this.$emit('allClick')
      } else {
        this.$emit('toGame', game)
      }
    },
    handleNext() {
      this.page++
      this.$nextTick(() => loadGameImg(this.isApp ? 6 : 12))
    },
    onGoFeatured() {
      if (this.disabled) return
      this.$emit('go-featured')
    },
    onClickBanner() {
      if (this.timer) return
      this.timer = setTimeout(() => {
        this.timer = null
        jump(this.banner, this, false)
      }, 350)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/menu.index.modules.scss';

.game-group-slider {
  margin-top: $game_group_slider_mt;
  .game-group-head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: $group_head_margin_bottom;
    .game-group-head--left {
      display: flex;
      align-items: center;
      line-height: $game_group_head_left_line;
      .game-group-ico {
        width: $group_ico_width;
        height: $group_ico_height;
        margin-right: $group_ico_margin_right;
      }
      .game-group-name {
        font-size: $group_name_font_size;
        font-weight: $group_name_font_weight;
      }
    }
    .game-group-head--action {
      display: flex;
      align-items: center;
      font-size: $group_action_font;
      font-weight: $group_action_font_weight;
      color: $group_action_color;
      line-height: $group_action_line;
      cursor: pointer;
      transition: color 0.3s;
      &:hover {
        color: $group_action_hover_color;
      }
      span {
        margin-right: $group_ico_margin_right;
      }
    }
  }
  .game-group-wrapper {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-left: -#{$game_wrapper_space}px;
    margin-right: -#{$game_wrapper_space}px;
    overflow-x: hidden;
    .game-wrapper {
      padding: $game_wrapper_padding;
      margin: $game_wrapper_margin;
    }
  }
  .game-group-action {
    display: flex;
    justify-content: center;
    margin: 16px 0 0 0;
    .btn--action {
      height: $btn__action_pc_height;
      line-height: $btn__action_pc_height;
      padding: $btn__action_pa_pc_padding;
      font-size: $btn__action_pc_font;
      font-weight: $btn__action_weight;
      color: $btn__action_color;
      background: $btn__action_background;
      border-radius: $btn__action_radius;
      transition: color 0.3s;

      &:hover {
        color: $btn__action_color_hover;
      }
    }
  }
  .banner_wrap {
    position: relative;
    width: $game_group_banner_pc_width;
    aspect-ratio: $game_group_banner_pc_ratio;
    border-radius: $game_group_banner_pc_radius;
    margin: $game_group_banner_pc_margin;
    overflow: hidden;
    --bannerUrl: '';
    &.gap {
      margin: $game_group_banner_gap_pc_margin;
    }
    .banner {
      width: $game_group_banner_pc_width;
      height: $game_group_banner_pc_height;
      background: var(--bannerUrl) no-repeat center / cover;
      cursor: pointer;
      transition: $game_group_banner_pc_transition;
      &:hover {
        transform: $game_group_banner_hover_pc_transform;
        filter: $game_group_banner_hover_pc_filter;
      }
      &:active {
        transform: $game_group_banner_active_pc_transform;
      }
    }
  }
}

@include breakpoint-only-xs() {
  .game-group-slider {
    margin-top: $xs_game_group_slider_mt;
    &.game_index {
      margin-top: $xs_game_group_slider_first_mt;
    }
    .game-group-wrapper {
      overflow-x: hidden;
      margin-left: -#{$game_wrapper_app_space}px;
      margin-right: -#{$game_wrapper_app_space}px;
      .game-wrapper {
        padding: $game_wrapper_app_padding;
        margin: $game_wrapper_app_margin;
      }
    }
    .game-group-action {
      margin: 10px 0 0 0;
      .btn--action {
        height: $btn__action_height;
        line-height: $btn__action_height;
        padding: $btn__action_pa;
        font-size: $btn__action_font;
      }
    }
    .banner_wrap {
      aspect-ratio: $game_group_banner_app_ratio;
      margin: $game_group_banner_app_margin;
      &.gap {
        margin: $game_group_banner_gap_app_margin;
      }
    }
  }
}
</style>
