<template>
  <div v-if="list.length" class="game-factory-slider">
    <div class="game-factory-head">
      <div class="game-factory-head--left">
        <span class="game-factory-name">{{ name }}</span>
        <div v-if="showControl" class="game-factory-control">
          <span><icon-font name="icon-Icon_left" :size="loadVariables('group_icon_size')" @click="handlePrev" /></span>
          <span
            ><icon-font
              class="cursor_point"
              name="icon-Icon_Right"
              :size="loadVariables('group_icon_size')"
              @click="handleNext"
          /></span>
        </div>
      </div>
      <!-- <div class="game-factory-head--action" @click="$emit('go-featured')">
				<span>{{ $t('UI_Load_more') }}</span>
				<icon-font name="icon-Icon_Right" size="16" />
			</div> -->
    </div>
    <div class="game-factory-wrapper">
      <div ref="scroll" class="scroll-wrapper">
        <div v-for="item in list" :key="item.id" class="factory-wrapper">
          <div class="factory-box" :style="{ width: cardWidth + 'px' }" @click="handleClick(item)">
            <img :src="item.img_url" :style="imgSize" />
            <span>{{ $t('UI_game_cout', { X: item.game_count }) }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import variables from '@style/views/menu.index.modules.scss'

const { game_wrapper_space, game_wrapper_app_space, lobby_box_padded, xs_lobby_box_padded } = variables

export default {
  name: 'GameFactory',
  components: {},
  props: {
    name: {
      type: String,
      default: ''
    },

    list: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      page: 0
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('layout', ['containerBox']),
    size() {
      // app 时不完全展示3个
      return this.isApp ? 2.72 : 6
    },
    cardWidth() {
      const size = this.size
      const spacing = this.isApp ? game_wrapper_app_space * 2 : game_wrapper_space * 2
      const outer_padded = this.isApp ? xs_lobby_box_padded * 2 : lobby_box_padded * 2
      const width = (this.containerBox.width - outer_padded + spacing) / size - spacing
      return width
    },
    imgSize() {
      const w = 132
      const h = 54
      const rate = h / w
      const spacing = 24
      const width = this.cardWidth - spacing
      return {
        width: `${width}px`,
        height: `${width * rate}px`
      }
    },
    showControl() {
      if (this.isApp) return false
      return this.list.length > this.size
    }
  },
  methods: {
    loadVariables(name) {
      return variables[name]
    },
    // type  大厅移动端更多信息为1
    toGame(game, type) {
      if (type == 1) {
        this.$emit('allClick')
      } else {
        this.$emit('toGame', game)
      }
    },
    handlePrev() {
      if (!this.page) return
      this.page--
      this.scrollWrapper()
    },
    handleNext() {
      if ((this.page + 1) * this.size >= this.list.length) return
      this.page++
      this.scrollWrapper()
    },
    scrollWrapper() {
      const $wrapper = this.$refs.scroll
      if (!$wrapper) return
      let x = 0
      const sut = this.list.length - this.page * this.size
      if (sut >= this.size) {
        x = 100 * this.page
      } else {
        x = 100 * this.page - (100 * (this.size - sut)) / this.size
      }
      $wrapper.style.transform = `translateX(-${x}%)`
    },
    handleClick(factory) {
      this.$emit('to-featured', factory)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/menu.index.modules.scss';

.game-factory-slider {
  // margin: $group_slider_margin;
  overflow-x: hidden;
  .game-factory-head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: $group_head_margin_bottom;
    .game-factory-head--left {
      display: flex;
      align-items: center;
      .game-factory-name {
        font-size: $group_name_font_size;
        font-weight: $group_name_font_weight;
      }
      .game-factory-control {
        display: flex;
        align-items: center;
        margin-left: $game_group_control_ml;
        > span {
          display: flex;
          align-items: center;
          color: $group_action_color;
          margin-right: $game_group_control_span_mr;
          cursor: pointer;
          transition: color 0.3s;
          &:hover {
            color: $group_action_hover_color;
          }
        }
      }
    }
  }
  .game-factory-wrapper {
    position: relative;
    margin-left: -#{$game_wrapper_space}px;
    margin-right: -#{$game_wrapper_space}px;
    .scroll-wrapper {
      display: flex;
      flex-wrap: nowrap;
      transition: transform 0.6s ease;
      .factory-wrapper {
        padding: $game_wrapper_padding;
        .factory-box {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          padding: $factory_box_padding;
          background: $factory_box_background;
          color: $factory_box_color;
          font-size: $factory_box_font_size;
          cursor: pointer;
          box-shadow: $factory_box_shadow;
          border-radius: $factory_box_radius;

          &:hover {
            // transform: scale(1.03);
            background: $factory_box_background_hover;
          }
          > span {
            margin-top: $factory_box_span_mt;
          }
        }
      }
    }
  }
}

@include breakpoint-only-xs() {
  .game-factory-slider {
    .game-factory-wrapper {
      overflow-x: hidden;
      overflow-y: hidden;
      margin-left: -#{$game_wrapper_app_space}px;
      margin-right: -#{$game_wrapper_app_space}px;
      &::after {
        content: '';
        position: absolute;
        top: 0;
        right: -#{$game_wrapper_space}px;
        width: $slide_mask_width;
        height: 100%;
        background: $slide_mask_background;
      }

      .scroll-wrapper {
        overflow-x: auto;
        .factory-wrapper {
          padding: $game_wrapper_app_padding;
        }
      }
    }
  }
}
</style>
