<template>
  <v-container ref="boxIds" class="lobby-box">
    <!-- 引导页 -->
    <template v-if="guidanceSwitch && !fourceRenderCasino">
      <welcome-hall @show-casino="fourceRenderCasino = true" />
    </template>
    <template v-else>
      <template v-if="isHorseRace">
        <horse-race-lamp />
      </template>
      <!-- 轮播图 -->
      <m-carousel />
      <!--游戏类型-->
      <template v-if="!isSpace">
        <game-control :list="tablistData" :disabled="controlDisabled" @change="changeTab" />
        <!-- lobboy 大厅 -->
        <template v-if="categoryType == GAME_TYPE.LOBBY">
          <!-- 最近游戏 -->
          <game-recent ref="gameRcentRef" :list="recent" @toGame="toGame" />
          <!-- 游戏分组 -->
          <game-group
            v-for="(item, i) in lobbyListData"
            :index="i"
            :key="item.id"
            :icon="item.icon"
            :name="item.type_name"
            :default-size="getGroupPageSize(item)"
            :list="item.game_list"
            :disabled="groupDisabled"
            :banner="item.banner"
            @go-featured="goFeatured(item)"
            @toGame="toGame($event, item)"
            @allClick="goFeatured(item)"
          >
            <div v-if="item.factory_is_show && otherFactory.length" class="group-factory-box">
              <game-factory :name="$t('UI_Factory')" :list="otherFactory" @to-featured="toFactoryFeatured" />
            </div>
          </game-group>
        </template>
        <template v-else>
          <div class="game-grid-box">
            <game-grid :list="gridList" :category="selectTab.category_type" @toGame="toGame">
              <button v-if="gridPageFinished" class="btn--theme" @click="handleGridNext">
                {{ $t('General.General_8') }}
              </button>
            </game-grid>
          </div>
          <div v-if="selectTab.factory_is_show && otherFactory.length" class="category-factory-box">
            <game-factory :name="$t('UI_Factory')" :list="otherFactory" @to-featured="toFactoryFeatured" />
          </div>
        </template>
      </template>
      <!-- 骨架屏 -->
      <template v-else>
        <space-ui sign="game_list" />
      </template>
    </template>
    <!-- 排行榜 -->
    <template v-if="isRanking">
      <ranking-list id="rankingListId" />
    </template>
  </v-container>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import ContainerMixin from '@/components/containerMixin'
import HorseRaceLamp from './ui/horse-race-lamp'
import MCarousel from './ui/m-carousel'
import GameControl from './ui/game-control'
import GameGroup from './ui/game-group'
import GameGrid from './ui/game-grid'
import GameFactory from './ui/game-factory'
import GameRecent from './ui/game-recent'
import RankingList from './ui/ranking-list'
import WelcomeHall from './ui/welcome-hall'
import gameAPI from '@/utils/game/gameAPI'
import spaceUi from '@/components/skeleton/space-ui.vue'
import { GAME_TYPE, GRID_LIST_PAGE_SIZE } from '@/api/setting'
import { AJ_DOTTING } from '@/utils/dotting.js'
import { cacheList, cacheLobbyList } from './data.js'
import loadGameImg from '@/utils/loadGameImg'

const GAME_LIST = []

export default {
  name: 'menuIndex',
  components: {
    WelcomeHall,
    HorseRaceLamp,
    MCarousel,
    GameControl,
    GameGroup,
    GameGrid,
    GameFactory,
    GameRecent,
    spaceUi,
    RankingList
  },
  mixins: [ContainerMixin],
  data() {
    return {
      GAME_TYPE,
      list: [],
      tab: 10,
      keywords: '',
      downloadDialogState: false,
      isSpace: true, // 是否显示骨架屏
      isHorseRace: false, // 是否加载广播组件
      gridPage: 1,
      categoryType: GAME_TYPE.LOBBY,
      fourceRenderCasino: true
      // GAME_TYPE.LOBBY
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    ...mapState('source', ['tablist', 'collectGames', 'recent', 'gamelist', 'lobbylist', 'otherFactory']),
    ...mapGetters('base', ['getShowConfig', 'guidanceSwitch']),
    tablistData() {
      return Array.isArray(this.tablist) && this.tablist.length ? this.tablist : cacheList
    },
    lobbyListData() {
      const arr =
        Array.isArray(this.lobbylist) && this.lobbylist.length
          ? JSON.parse(JSON.stringify(this.lobbylist))
          : cacheLobbyList
      arr.forEach(e => {
        if (Array.isArray(e.game_list)) {
          const game_list = e.game_list
          e.game_list = game_list.map(item => {
            return {
              ...item,
              keys: `${e.id}_${Math.random()}`
            }
          })
        } else {
          e.game_list = []
        }
      })
      return arr
    },
    selectTab() {
      let tablistData = this.tablistData.find(item => item.id === this.tab)
      return tablistData
    },
    // 游戏列表 获取 Lobby 的列表，并且把收藏排除
    lobbtList() {
      const WHITE_GAME_LIST = [0, 11]
      return this.tablistData.filter(v => !WHITE_GAME_LIST.includes(v.id))
    },
    // 游戏列表 获取除 Lobby 外的列表
    exceptLobbt() {
      const except = this.tablist.filter(f => f.id != 0)
      return except
    },
    gridSize() {
      return GRID_LIST_PAGE_SIZE
    },
    gridList() {
      let ret = []
      const size = this.gridSize
      const startIndex = 0
      const endIndex = this.gridPage * size
      const list = this.categoryType == GAME_TYPE.FAVORATE ? this.collectGames : this.gamelist
      ret = list.slice(startIndex, endIndex).map(v => ({ ...v, keys: `${v.gameId}_${Math.random()}` }))
      return ret
    },
    gridPageFinished({ gamelist, gridSize, gridPage }) {
      return gamelist.length > gridSize * gridPage
    },
    controlDisabled() {
      return !(Array.isArray(this.tablist) && this.tablist.length)
    },
    groupDisabled() {
      return !(Array.isArray(this.lobbylist) && this.lobbylist.length)
    },
    /**
     * 底部排行开关
     * ranking_show_status 			下注排行和赢钱最多排行开关 1开 0关
     * activity_agent_rank_switch 	全民代排行榜开关 1开 0关
     */
    isRanking() {
      const { ranking_show_status = 0, activity_agent_rank_switch = 0 } = this.systemConfig

      return ranking_show_status == 1 || activity_agent_rank_switch == 1
    }
  },
  watch: {
    $route: {
      handler(newRou, oldRou) {
        const { query } = newRou
        if (query.ref === 'casino') {
          this.fourceRenderCasino = true
          this.$router.replace({ name: 'menu' })
        }
        // 当前页锚点定位
        if (this.tablist.length && query.id) {
          const target = document.querySelector(`#link_${query.id}`)
          target && target.scrollIntoView()
        }
      },
      deep: true,
      immediate: true
    },
    // 监听游戏数据, 去除游戏列表的最小高度设置
    tablist: {
      handler(nTab, oTab) {
        this.isSpace = false
        if (nTab) {
          setTimeout(() => {
            const mTabsRef = this.$refs['mTabsRef']
            if (mTabsRef) {
              mTabsRef.$el.style.minHeight = 'auto'
            }
          }, 500)
        }
      },
      deep: true,
      immediate: true
    },
    isApp: {
      handler() {
        setTimeout(() => loadGameImg(this.isApp ? 6 : 12), 30)
      }
    },
    recent: {
      handler() {
        setTimeout(() => loadGameImg(this.isApp ? 6 : 12), 30)
      }
    },
    lobbylist: {
      handler(newData) {
        if (newData.length > 0) {
          this.lobbylist.length && this.$nextTick(() => loadGameImg(this.isApp ? 6 : 12))
        }
      },
      deep: true,
      immediate: true
    }
  },
  created() {
    // 处理首页是否进入引导过
    if (this.user.uid) {
      const storageKey = 'first_guide_redirect_' + this.user.uid
      if (localStorage.getItem(storageKey) === 'menu') {
        this.fourceRenderCasino = true
      }
    }
    // if (this.lobbylist.length == 0) {
    // 	gameAPI.initGame(async (res = false) => {
    // 		await gameAPI.getTabs()
    // 		await gameAPI.getLobbyList()
    // 		if(res == true){
    // 			setTimeout(() =>{
    // 				gameAPI.favorateUp()
    // 			},100)
    // 		}
    // 		this.isMounted && this.lobbylist.length && this.$nextTick(() => loadGameImg(this.isApp ? 6 : 12))
    // 	})
    // }
    const tabItem = this.tablist.find(v => v.category_type == this.categoryType) || {}
    this.tab = tabItem.id
  },
  activated() {
    // 判断广播是否加载。【开关开启 && app端】
    const isSwitch = this.getShowConfig('lantern_btn') == 1
    this.isHorseRace = isSwitch && this.isApp
    // 其他页面返回，图片分批加载
    setTimeout(() => loadGameImg(this.isApp ? 6 : 12), 30)
    // 该页面失活 recent 数组有新增时vm也会随之更新，但此时 game-card 组件内部使用的textfit方法将无法获取真实dom的宽高
    // 在组件激活时手动调用game-card内部的createdTextfit方法
    if (this.$refs.gameRcentRef && Array.isArray(this.$refs.gameRcentRef.$refs.gameCardRef)) {
      this.$nextTick(() => {
        this.$refs.gameRcentRef.$refs.gameCardRef.map(instance => instance.createdTextfit())
      })
    }
  },
  deactivated() {
    this.isHorseRace = false
  },
  methods: {
    getGroupPageSize(data) {
      if (this.isApp) {
        return data.show_game_num || 12
      } else if (data.show_game_num) {
        // pc 且 show_game_num 有值;
        // show_game_num 是6的倍数则取 show_game_num，如果不是则取 6；
        const remainder = data.show_game_num % 6
        return remainder === 0 ? data.show_game_num : 6
      }
      return 6
    },
    getListLength(params) {
      const od = GAME_LIST
      const rt = od.find(v => v.type_id == params.type_id)
      let list = []
      if (params.type_id == 0) {
        list = GAME_LIST
      } else {
        list = rt ? rt.game_list : []
      }
      return list.length
    },
    // 分类游戏分页加载
    handleGridNext() {
      AJ_DOTTING.getAjdata('CLASSIFICATION_TAB', this.tab)
      this.gridPage++
    },
    // 切换 tab 更新gamelist,与下面跳转都是存到 gamelist 里，
    // 不知道这里删除会不会影响到其他地方，所以不改动
    changeTab(params) {
      AJ_DOTTING.getAjdata('CLASSIFICATION_TAB', params.id)

      this.categoryType = params.category_type
      this.tab = params.id
      this.gridPage = 1
      if (params.category_type == GAME_TYPE.FAVORATE) {
        this.$store.commit('source/SET_GAMELIST', this.collectGames)
      } else if (params.category_type != GAME_TYPE.LOBBY) {
        gameAPI.findSpecialGame(params.id, params.category_type)
      }

      if (this.categoryType === GAME_TYPE.LOBBY) {
        this.$nextTick(() => loadGameImg(this.isApp ? 6 : 12))
      }
    },
    // 点击更多，跳转
    goFeatured({ category_type, id, factory_is_show: factoryIsShow, type_name }) {
      AJ_DOTTING.getAjdata('CLASSIFICATION_ALL', id)

      // return
      if (category_type == GAME_TYPE.FAVORATE) {
        this.$router.push({ name: 'featured', query: { id, factoryIsShow, type_name } })
      } else {
        gameAPI.findSpecialGame(id, category_type).then(_ => {
          this.$router.push({ name: 'featured', query: { id, factoryIsShow, type_name } })
        })
      }
    },
    goPesquisa({ name }) {
      this.$router.push({
        name: 'pesquisa',
        query: {
          keywords: name
        }
      })
    },
    /**
     * @desc 由于进入游戏前需要和服务接口交互获取准确的地址，不同模块的游戏接口是不一致的，需要根据游戏类型做分类处理
     * 未做事项:
     *  1.未根据游戏类型调用接口
     *  2.点击游戏卡时，弹出加载游戏的弹窗提示
     * game 游戏信息
     * item 厂家信息
     * */
    toGame(game = {}, item = {}) {
      this.$store.dispatch('game/enterTheGame', { game, vm: this })
    },
    toFactoryFeatured(data) {
      this.$router.push({
        name: 'featured',
        query: { render: 'factory', factory_id: data.id, type_name: data.name }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/menu.index.modules.scss';
.lobby-box {
  padding: $lobby_box_pa;
  margin-top: $lobby_box_mt;
  position: relative;
}
.group-factory-box {
  margin-top: $group_factory_box_mt;
}
.category-factory-box {
  margin-top: $category_factory_box_mt;
}

.game-grid-box {
  margin-top: $game_grid_box_mt;
}

@include breakpoint-only-xs() {
  .lobby-box {
    padding: $xs_lobby_box_pa;
    margin-top: $xs_lobby_box_mt;
  }
  .group-factory-box {
    margin-top: $xs_group_factory_box_mt;
  }
  .game-grid-box {
    margin-top: $xs_game_grid_box_mt;
  }
}
</style>
