<template>
  <div class="vipbox" :class="{ vipbox_keep_level: isApp && isKeepLevel }">
    <div class="vipbox_title">
      <span>{{ vipLevel }}</span>
    </div>
    <!-- 保级 -->
    <template v-if="isKeepLevel">
      <div class="keep_tip_block">
        <div class="keep_tip_text">{{ $t('UI_VIP_Keep_Level_Text') }}</div>
        <v-btn class="keep_recharge_btn" color="t_primary_dark_color" @click="onRechargeWithdrawal">
          {{ $t('UI_VIP_Recarega_Btn') }}
        </v-btn>
      </div>

      <div class="vip_process">
        <div class="process_text">
          <div class="process_lt">{{ $t('UI.Charge_0_main_3') }}</div>
          <div class="process_rt">
            {{ $u.formatKilobit(currentProcess.week_recharge, isKilobit) }}
            /
            {{ $u.formatKilobit(currentProcess.lv_keep_recharge, isKilobit) }}
          </div>
        </div>
        <v-progress-linear
          color="t_primary_dark_color"
          background-color="t_process_bg"
          :height="isApp ? 6 : 10"
          :value="(currentProcess.week_recharge / currentProcess.lv_keep_recharge) * 100"
          rounded
        >
        </v-progress-linear>
      </div>
    </template>
    <template v-else>
      <template v-if="endLevel">
        <div class="tip_text">{{ $t('UI_VIP_Reach_highest') }}</div>
      </template>
      <template v-if="!endLevel && nextProcess">
        <div class="vip_process">
          <div class="process_text">
            <div class="process_lt">
              {{ $t('UI_VIP_Vip.deposit') }}
            </div>
            <div class="process_rt">
              {{ $u.formatKilobit(nextProcess.recharge_gold, isKilobit) }}
              /
              {{ $u.formatKilobit(nextProcess.min_recharge, isKilobit) }}
            </div>
          </div>
          <v-progress-linear
            color="t_primary_dark_color"
            background-color="t_process_bg"
            :height="isApp ? 6 : 10"
            :value="(nextProcess.recharge_gold / nextProcess.min_recharge) * 100"
            rounded
          />
        </div>
        <div class="vip_process">
          <div class="process_text">
            <div class="process_lt">
              {{ $t('UI_VIP_Vip.bet') }}
            </div>
            <div class="process_rt">
              {{ $u.formatKilobit(nextProcess.vip_bets, isKilobit) }}
              /
              {{ $u.formatKilobit(nextProcess.min_bet, isKilobit) }}
            </div>
          </div>
          <v-progress-linear
            color="t_primary_dark_color"
            background-color="t_process_bg"
            :height="isApp ? 6 : 10"
            :value="(nextProcess.vip_bets / nextProcess.min_bet) * 100"
            rounded
          />
        </div>
      </template>
    </template>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import PublishSubscribe from 'publish-subscribe-js'
import VipType from '@/events/vip'
import { vipList as getMineVipList } from '@/api/modules/bet.js'

export default {
  data() {
    return {
      vipList: [],
      isKilobit: false
    }
  },
  computed: {
    ...mapState('base', ['user', 'isApp']),
    ...mapGetters('base', ['getVipCurrentLevel']),
    /**
     * 是否正在保级中
     * is_keep_level 0保级中 1未保级
     */
    isKeepLevel() {
      const { is_keep_level = 0 } = this.user
      return is_keep_level != 1
    },

    // 当前的vip等级
    vipLevel() {
      const lv = this.$t('UI_VIP_Vip.lv')
      return lv + this.getVipCurrentLevel()
    },
    vipIndex() {
      const { vip_id } = this.user
      const fIndex = this.vipList.findIndex(f => f.id == vip_id)
      return fIndex
    },
    // 最高等级
    endLevel() {
      return this.vipList.length && this.vipIndex === this.vipList.length - 1
    },
    currentProcess() {
      return this.vipList[this.vipIndex] || {}
    },
    nextProcess() {
      return this.vipList[this.vipIndex + 1] || {}
    }
  },
  created() {
    this.getVipList()
  },
  mounted() {
    // 发布订阅, 更新vip列表
    this.renewList = () => {
      this.getVipList()
    }
    PublishSubscribe.subscribe(VipType.RENEW_LIST, this.renewList)
  },
  beforeDestroy() {
    // 卸载订阅
    PublishSubscribe.unsubscribe(VipType.RENEW_LIST)
  },
  methods: {
    // 充值提现
    onRechargeWithdrawal() {
      this.$store.dispatch('modal/setDepositWithdrawShow')
    },
    // 请求vip数据
    async getVipList() {
      const params = {
        token: this.user.token,
        language: this.languageType
      }

      const res = await getMineVipList(params).catch(e => e)
      const { code, data } = res
      if (code != 0) return
      // vip_bets          pc端 当前打码量
      // max_recharge      pc端 最大充值数
      // recharge_gold     pc端 当前充值数
      // vip_id            当前等级对应的列表 id
      // vip_faq_name      根据这个字段获取VIP保级降级功能说明
      // lv_keep_recharge  开启保级后，当前等级要充值多少钱才能解开保级 ------- 保级使用
      // week_recharge     本周已经充值了多少钱 ------------------------------ 保级使用
      // thousand_mark 	 vip千分位开关 0关 1开
      const list = data.list && Array.isArray(data.list) ? data.list : []
      const vipListOld = data.show_vip_list && Array.isArray(data.show_vip_list) ? data.show_vip_list : []
      // 合并两个列表的数据
      const vipListNew = vipListOld.map(item => {
        const vipItem = list.find(f => f.id == item.id)
        const newItem = { ...vipItem, ...item }
        return {
          ...newItem,
          recharge_gold: data.recharge_gold, // 当前充值数
          vip_bets: data.vip_bets, // 当前打码
          week_recharge: data.week_recharge // 当前打码
        }
      })
      this.vipList = vipListNew
      this.isKilobit = data.thousand_mark == 1
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/menu.welcome_hall.modules.scss';
.vipbox_keep_level {
  width: 100% !important;
}
.vipbox_title {
  font-size: $vipbox_title_fs;
  font-weight: 600;
  margin-bottom: $vipbox_title_mb;
}
.vip_process + .vip_process {
  margin-top: $vipbox_title_mb;
}
.process_text {
  display: flex;
  justify-content: space-between;
  margin-bottom: $process_text_mb;
}

.process_lt {
  color: $colorText_meta_item;
}

.tip_text {
  font-size: $tip_text_fs;
  color: $colorText_meta_item;
  margin-bottom: $tip_text_mb;
}
.keep_tip_block {
  display: flex;
  align-items: flex-end;
  gap: $keep_tip_block_gap;
  justify-content: space-between;
  margin-bottom: $keep_tip_block_mb;
}

@include breakpoint-only-xs() {
  .keep_tip_block {
    margin-bottom: $keep_tip_block_mb_xs;
  }
  .keep_tip_text {
    font-size: $keep_tip_text_fs_xs;
  }
  .vipbox_title {
    font-size: $vipbox_title_fs_xs;
    margin-bottom: $vipbox_title_mb_xs;
  }
  .vip_process + .vip_process {
    margin-top: 6px;
  }
  .process_text {
    font-size: $process_text_fs_xs;
    margin-bottom: $process_text_mb_xs;
  }
  .highest_text {
    font-size: $highest_text_fs_xs;
  }

  .vipbox_keep_level {
    .vipbox_title {
      margin-bottom: 0;
    }
    .keep_recharge_btn {
      border-radius: 8px;
    }
  }
}
</style>
