<template>
  <div class="comp ranking_list">
    <div class="ranking_tab_list">
      <div
        class="ranking_list_tab"
        :class="{ btn_click_state: isApp, active: active == item.active }"
        v-for="item in tabList"
        :key="item.id"
        @click="onTab(item)"
      >
        <template v-if="!isApp">
          <img class="tab_icon" :src="loadIcon(item.icon)" alt="" />
        </template>
        <span class="tab_text">{{ item.name }}</span>
      </div>
    </div>
    <div class="ranking_table">
      <template v-if="isAgentRankShow">
        <!-- 全民代排名 -->
        <div v-show="active == 1 || !isApp" class="rank_table">
          <div class="ranking_head">
            <div class="ranking_head_item" v-for="(head, i) in rankHead" :key="i">
              {{ head.label }}
            </div>
          </div>
          <div class="ranking_table_list">
            <div class="ranking_table_item" v-for="(item, i) in rankList" :key="i">
              <div class="rank">
                <div class="rank_box">
                  <template v-if="i == 0 || i == 1 || i == 2">
                    <img class="rank_img" :src="loadIcon(`menu_award_${i + 1}`)" alt="" />
                  </template>
                  <template v-else>
                    <span class="rank_text">{{ item.rank }}</span>
                  </template>
                </div>
              </div>
              <div class="player">
                <div class="player_img">
                  <user-sprite-image :value="item.headimg" :size="isApp ? 24 : 36" />
                </div>
                <span class="player_text text_1">{{ item.username }}</span>
              </div>
              <div class="member">{{ item.total_num }}</div>
            </div>
          </div>
        </div>
      </template>
      <template v-if="isRankingShow">
        <!-- 赢钱最多排名 -->
        <div v-show="active == 2 || !isApp" class="round_table">
          <div class="round_head">
            <div class="round_head_item" v-for="(head, i) in roundHead" :key="i">
              {{ head.label }}
            </div>
          </div>
          <div ref="roundBoxRef" class="round_table_box">
            <div ref="roundListRef" class="round_table_list" :class="{ roll: isRoll, no_roll: !isRoll }">
              <div class="round_table_item" v-for="(item, i) in roundList" :key="i">
                <div class="game">
                  <img class="game_img" :src="item.pic_url" alt="" />
                  <span class="game_name text_1">{{ item.game_name }}</span>
                </div>
                <div class="player">
                  <div class="player_img">
                    <user-sprite-image :value="item.headimg" :size="isApp ? 24 : 36" />
                  </div>
                  <span class="player_text text_1">{{ item.nickname }}</span>
                </div>
                <div class="amount">{{ item.total_bet_gold }}</div>
              </div>
            </div>
          </div>
        </div>
        <!-- 获奖排名 -->
        <div v-show="active == 3 || !isApp" class="win_table">
          <div class="win_head">
            <div class="win_head_item" v-for="(head, i) in winHead" :key="i">
              {{ head.label }}
            </div>
          </div>
          <div class="win_table_list">
            <div class="win_table_item" v-for="(item, i) in winList" :key="i">
              <div class="rank">
                <div class="rank_box">
                  <template v-if="i == 0 || i == 1 || i == 2">
                    <img class="rank_img" :src="loadIcon(`menu_award_${i + 1}`)" alt="" />
                  </template>
                  <template v-else>
                    <span class="rank_text">{{ i + 1 }}</span>
                  </template>
                </div>
              </div>
              <div class="player">
                <div class="player_img">
                  <user-sprite-image :value="item.headimg" :size="isApp ? 24 : 36" />
                </div>
                <span class="player_text text_1">{{ item.nickname }}</span>
              </div>
              <div class="profit">{{ item.game_profit }}</div>
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getAgentRankListCache, getLatestRoundList, getBiggestToDayListCache } from '@/api/modules/config'
import loadIcon from '@/assets/icons'
import UserSpriteImage from '@/components/ui/userSpriteImage/user-sprite-image'
import _common from '@style/views/menu.index.modules.scss'

export default {
  components: {
    UserSpriteImage
  },
  data() {
    return {
      active: null, // 1: 全民代排名 2: 赢钱最多排名 3: 获奖排名
      rankList: [], // 全民代排名 list
      roundList: [], // 赢钱最多排名 list
      winList: [], // 获奖排名 list
      isRoll: false, // 赢钱最多排名是否滚动
      roundRate: 50 // 赢钱最多排名滚动速度 50px/s
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    // 下注排行和赢钱最多排行开关 1开 0关
    isRankingShow() {
      const { ranking_show_status = 0 } = this.systemConfig

      return ranking_show_status == 1
    },
    // 全民代排行榜开关 1开 0关
    isAgentRankShow() {
      const { activity_agent_rank_switch = 0 } = this.systemConfig

      return activity_agent_rank_switch == 1
    },
    // tab列表
    tabList() {
      const list = [
        {
          active: 1,
          name: this.$t('UI_Ranking_Tab_Ranking'),
          icon: 'agent_icon',
          hidden: this.isAgentRankShow
        },
        {
          active: 2,
          name: this.$t('UI_Ranking_Tab_Record'),
          icon: 'record_icon',
          hidden: this.isApp && this.isRankingShow
        },
        {
          active: 3,
          name: this.$t('UI_Ranking_Tab_Winning'),
          icon: 'win_icon',
          hidden: this.isRankingShow
        },
        {
          active: 2,
          name: this.$t('UI_Ranking_Tab_Record'),
          icon: 'record_icon',
          hidden: !this.isApp && this.isRankingShow
        }
      ]

      return list.filter(f => f.hidden)
    },
    // 全民代排名head
    rankHead() {
      return [
        {
          label: this.$t('UI_Ranking_Table_Rank'),
          prop: 'rank'
        },
        {
          label: this.$t('UI_Ranking_Table_Player'),
          prop: 'username'
        },
        {
          label: this.$t('UI_Ranking_Table_Members'),
          prop: 'total_num'
        }
      ]
    },
    // 赢钱最多排名 head
    roundHead() {
      return [
        {
          label: this.$t('UI_Ranking_Table_Game'),
          prop: 'game_name'
        },
        {
          label: this.$t('UI_Ranking_Table_Player'),
          prop: 'nickname'
        },
        {
          label: this.$t('UI_Ranking_Table_Amount'),
          prop: 'total_bet_gold'
        }
      ]
    },
    // 获奖排名head
    winHead() {
      return [
        {
          label: this.$t('UI_Ranking_Table_Rank'),
          prop: 'sort'
        },
        {
          label: this.$t('UI_Ranking_Table_Player'),
          prop: 'nickname'
        },
        {
          label: this.$t('UI_Ranking_Table_Profit'),
          prop: 'game_profit'
        }
      ]
    }
  },
  created() {
    this.active = this.isAgentRankShow ? 1 : this.isRankingShow ? 2 : 3
    if (this.isAgentRankShow) this.getRankList()
    if (this.isRankingShow) {
      this.getRoundList(() => {
        if (this.isApp) {
          if (this.active == 2) this.getRoundRoll(1)
        } else {
          this.getRoundRoll(1)
        }
      })
      this.getWinList()
    }
  },
  methods: {
    commonStyle: k => _common[k],
    loadIcon: (key, bool = false) => loadIcon(key, bool),
    // tab切换
    onTab(item = {}) {
      if (!this.isApp) return

      this.active = item.active
      if (this.active == 2) {
        // 判断是否开始/继续滚动
        this.getRoundRoll(1)
      } else {
        // 暂停滚动
        this.isRoll = false
        this.$nextTick(() => {
          const roundListRef = this.$refs['roundListRef']
          if (roundListRef) roundListRef.style.transform = `translateY(0px)`
        })
      }
    },
    /**
     * 计算赢钱最多排名盒子范围，来控制滚动动画
     * @param {*} num 是第几次计算
     */
    getRoundRoll(num) {
      this.$nextTick(() => {
        setTimeout(() => {
          const roundBoxRef = this.$refs['roundBoxRef']
          const roundListRef = this.$refs['roundListRef']
          if (!roundBoxRef || !roundListRef) return
          roundListRef.removeEventListener(
            'animationend',
            typeof this.animationendFun == 'function' ? this.animationendFun : () => {}
          )

          const box_h = roundBoxRef.clientHeight || roundBoxRef.offsetHeight
          const list_h = roundListRef.clientHeight || roundListRef.offsetHeight

          if (list_h > box_h) {
            // 滚动开始距离
            const startHeight = roundListRef.style.getPropertyValue('--roll-end')
            const start_h = num == 1 ? 0 : startHeight ? parseFloat(startHeight) : 0
            roundListRef.style.setProperty('--roll-start', `${start_h}px`)
            // 滚动结束距离
            const end_h = list_h - box_h
            roundListRef.style.setProperty('--roll-end', `-${end_h}px`)
            // 时长计算
            const roundTime = (Math.abs(end_h) - Math.abs(start_h)) / this.roundRate
            roundListRef.style.setProperty('--roll-time', `${roundTime}s`)

            this.isRoll = true

            // 监听动画是否结束
            this.animationendFun = () => {
              this.isRoll = false
              roundListRef.style.transform = `translateY(-${end_h}px)`
              this.getRoundList(() => {
                this.getRoundRoll()
              })
            }
            roundListRef.addEventListener('animationend', this.animationendFun)
          }
        }, 500)
      })
    },
    // 获取全民代排名列表
    async getRankList() {
      const params = { token: this.user.token, language: this.languageType }
      const res = await getAgentRankListCache(params).catch(e => e)
      if (!res || res.code != 0) return

      const resList = res.data && Array.isArray(res.data) ? res.data : []
      this.rankList = resList.splice(0, 10)
    },
    // 获取赢钱最多排名列表
    async getRoundList(callback) {
      const params = { token: this.user.token, language: this.languageType, limit: 200 }
      const res = await getLatestRoundList(params).catch(e => e)
      if (!res || res.code != 0) return

      const resList = res.data && Array.isArray(res.data) ? res.data : []
      const list = resList.length > 0 ? resList : this.roundList

      // 删除旧数据，防止页面dom节点太多 - 注意要留下一页的数据
      const roundBoxRef = this.$refs['roundBoxRef']
      const roundListRef = this.$refs['roundListRef']
      if (roundBoxRef && roundListRef) {
        const box_h = roundBoxRef.clientHeight || roundBoxRef.offsetHeight
        const list_h = roundListRef.clientHeight || roundListRef.offsetHeight
        if (list_h > box_h) {
          // 每一条数据的高度
          const item_h = list_h / this.roundList.length
          // 可删除的条数，多留一条数据做为缓冲
          const end_h = list_h - box_h
          let num = Math.floor(end_h / item_h)
          num = num > 0 ? num - 1 : 0

          this.roundList = this.roundList.slice(num)
          roundListRef.style.setProperty('--roll-end', `-${end_h - item_h * num}px`)
          roundListRef.style.transform = `translateY(-${end_h - item_h * num}px)`
        }
      }

      this.roundList = this.roundList.concat(list)
      if (typeof callback == 'function') callback()
    },
    // 获取获奖排名列表
    async getWinList() {
      const params = { token: this.user.token, language: this.languageType }
      const res = await getBiggestToDayListCache(params).catch(e => e)
      if (!res || res.code != 0) return

      const resList = res.data && Array.isArray(res.data) ? res.data : []
      this.winList = resList.splice(0, 10)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/menu.index.modules.scss';

.comp.ranking_list {
  width: $menu_ranking_list_app_width;
  margin: $menu_ranking_list_app_margin;
  padding: $menu_ranking_list_app_padding;
  border-radius: $menu_ranking_list_app_radius;
  background: $menu_ranking_list_app_bg;
  user-select: none;
  .ranking_tab_list {
    width: $menu_ranking_tab_list_app_width;
    height: $menu_ranking_tab_list_app_height;
    border-radius: $menu_ranking_tab_list_app_radius;
    background: $menu_ranking_tab_list_app_bg;
    @include setBox(row, center, center);
    .ranking_list_tab {
      flex: 1;
      height: $menu_ranking_tab_list_tab_app_height;
      padding: $menu_ranking_tab_list_tab_app_padding;
      border-radius: $menu_ranking_tab_list_tab_app_radius;
      cursor: pointer;
      @include setBox(row, center, center);
      &.active {
        background: $menu_ranking_tab_list_tab_active_app_bg;
        .tab_text {
          color: $menu_ranking_tab_list_tab_active_text_app_color;
        }
      }
      .tab_text {
        font-size: $menu_ranking_tab_list_tab_text_app_size;
        font-weight: $menu_ranking_tab_list_tab_text_app_weight;
        color: $menu_ranking_tab_list_tab_text_app_color;
        line-height: $menu_ranking_tab_list_tab_text_app_line;
        text-align: $menu_ranking_tab_list_tab_text_app_align;
      }
    }
  }
  .ranking_table {
    width: $menu_ranking_box_table_app_width;
    margin: $menu_ranking_box_table_app_margin;
    .rank_table,
    .round_table,
    .win_table {
      width: $menu_ranking_son_table_app_width;
      padding: $menu_ranking_son_table_app_padding;
      .ranking_head,
      .round_head,
      .win_head {
        width: $menu_ranking_son_table_head_app_width;
        @include setBox(row, center, center);
        .ranking_head_item,
        .round_head_item,
        .win_head_item {
          flex: 1;
          padding: $menu_ranking_son_table_head_item_app_padding;
          font-size: $menu_ranking_son_table_head_item_app_size;
          font-weight: $menu_ranking_son_table_head_item_app_weight;
          color: $menu_ranking_son_table_head_item_app_color;
          line-height: $menu_ranking_son_table_head_item_app_line;
          text-align: $menu_ranking_son_table_head_item_app_align;
          &:first-child {
            text-align: $menu_ranking_son_table_head_item_first_app_align;
          }
          &:last-child {
            text-align: $menu_ranking_son_table_head_item_last_app_align;
          }
        }
      }
      .ranking_table_list,
      .round_table_box,
      .win_table_list {
        width: $menu_ranking_son_table_list_app_width;
        height: $menu_ranking_son_table_list_app_height;
        overflow: hidden;
        .ranking_table_item,
        .round_table_item,
        .win_table_item {
          width: $menu_ranking_son_table_list_item_app_width;
          @include setBox($direction: row, $justify: center);
          .rank,
          .player,
          .member,
          .game,
          .amount,
          .profit {
            flex: 1;
            padding: $menu_ranking_son_table_list_item_gross_app_padding;
            font-size: $menu_ranking_son_table_list_item_gross_app_size;
            font-weight: $menu_ranking_son_table_list_item_gross_app_weight;
            color: $menu_ranking_son_table_list_item_gross_app_color;
            line-height: $menu_ranking_son_table_list_item_gross_app_line;
            overflow: hidden;
            @include setBox(row, flex-start, center);
            .rank_box {
              width: $menu_ranking_son_table_list_item_gross_rank_app_width;
              @include setBox(row, center, center);
              .rank_img {
                width: $menu_ranking_son_table_list_item_gross_rank_img_app_width;
              }
              .rank_text {
                font-size: $menu_ranking_son_table_list_item_gross_rank_text_app_size;
                font-weight: $menu_ranking_son_table_list_item_gross_rank_text_app_weight;
                color: $menu_ranking_son_table_list_item_gross_rank_text_app_color;
                line-height: $menu_ranking_son_table_list_item_gross_rank_text_app_line;
              }
            }
            .player_img {
              position: relative;
              &:before {
                content: '';
                width: $menu_ranking_son_table_list_item_gross_player_img_before_app_width;
                height: $menu_ranking_son_table_list_item_gross_player_img_before_app_height;
                position: $menu_ranking_son_table_list_item_gross_player_img_before_app_position;
                left: $menu_ranking_son_table_list_item_gross_player_img_before_app_left;
                top: $menu_ranking_son_table_list_item_gross_player_img_before_app_top;
                z-index: 2;
                background: $menu_ranking_son_table_list_item_gross_player_img_before_app_bg;
              }
            }
            .player_text {
              margin: $menu_ranking_son_table_list_item_gross_player_text_app_margin;
              font-size: $menu_ranking_son_table_list_item_gross_player_text_app_size;
              font-weight: $menu_ranking_son_table_list_item_gross_player_text_app_weight;
              color: $menu_ranking_son_table_list_item_gross_player_text_app_color;
              line-height: $menu_ranking_son_table_list_item_gross_player_text_app_line;
            }
            .game_img {
              flex-shrink: 0;
              flex-grow: 0;
              width: $menu_ranking_son_table_list_item_gross_game_img_app_width;
            }
            .game_name {
              flex-shrink: 1;
              flex-grow: 1;
              padding: $menu_ranking_son_table_list_item_gross_game_name_app_padding;
              font-size: $menu_ranking_son_table_list_item_gross_game_name_app_size;
              font-weight: $menu_ranking_son_table_list_item_gross_game_name_app_weight;
              color: $menu_ranking_son_table_list_item_gross_game_name_app_color;
              line-height: $menu_ranking_son_table_list_item_gross_game_name_app_line;
            }
          }
          .player {
            @include setBox(row, center, center);
          }
          .member,
          .amount,
          .profit {
            @include setBox(row, right, center);
          }
        }
        .round_table_list {
          @keyframes round-roll {
            0% {
              transform: translateY(var(--roll-start));
            }
            100% {
              transform: translateY(var(--roll-end));
            }
          }
          &.roll {
            animation-name: round-roll;
            animation-duration: var(--roll-time);
            animation-timing-function: linear;
            animation-iteration-count: 1;
            animation-fill-mode: forwards;
            animation-play-state: running;
          }
          &.no_roll {
            animation: none;
          }
        }
      }
    }
  }
}

// pc 端兼容样式
