  .comp.ranking_list {
    margin: $menu_ranking_list_pc_margin;
    padding: $menu_ranking_list_pc_padding;
    border-radius: $menu_ranking_list_pc_radius;
    background: none;
    .ranking_tab_list {
      height: $menu_ranking_tab_list_pc_height;
