      .rank_table,
      .round_table,
      .win_table {
        width: $menu_ranking_son_table_pc_width;
        padding: $menu_ranking_son_table_pc_padding;
        background: $menu_ranking_son_table_pc_bg;
        border-radius: $menu_ranking_son_table_pc_radius;
        .ranking_head,
        .round_head,
        .win_head {
          width: $menu_ranking_son_table_head_pc_width;
