          .win_table_item {
            width: $menu_ranking_son_table_list_item_pc_width;
            @include setBox($direction: row, $justify: center);
            .rank,
            .player,
            .member,
            .game,
            .amount,
            .profit {
              padding: $menu_ranking_son_table_list_item_gross_pc_padding;
              font-size: $menu_ranking_son_table_list_item_gross_pc_size;
              .rank_box {
                width: $menu_ranking_son_table_list_item_gross_rank_pc_width;
                @include setBox(row, center, center);
                .rank_img {
                  width: $menu_ranking_son_table_list_item_gross_rank_img_pc_width;
                }
                .rank_text {
                  font-size: $menu_ranking_son_table_list_item_gross_rank_text_pc_size;
                }
              }
              .player_text {
                margin: $menu_ranking_son_table_list_item_gross_player_text_pc_margin;
                font-size: $menu_ranking_son_table_list_item_gross_player_text_pc_size;
              }
              .game_img {
                flex-shrink: 0;
                flex-grow: 0;
                width: $menu_ranking_son_table_list_item_gross_game_img_pc_width;
              }
              .game_name {
                padding: $menu_ranking_son_table_list_item_gross_game_name_pc_padding;
