<template>
  <div>
    <template v-if="!isSpace">
      <swiper ref="pageSwiperRef" class="m-swiper" :options="swiperOptions">
        <swiper-slide
          v-for="(item, index) in deskNotices"
          :key="index"
          class="m-swiper-slide"
          :style="{
            backgroundImage: `url(${item.image})`
          }"
        >
          <v-responsive :aspect-ratio="slide_ratio" :data-item="index" />
        </swiper-slide>
        <div slot="pagination" class="swiper-pagination"></div>
      </swiper>
    </template>
    <template v-else>
      <space-ui sign="m_carousel" :style-opt="{ height: slide_height }" />
    </template>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import { jump } from '@/utils/linkMap'
import 'swiper/css/swiper.css'
import spaceUi from '@/components/skeleton/space-ui.vue'
import variables from '@style/views/menu.carousel.modules.scss'

const { slide_height, slide_ratio } = variables
let vm = this
export default {
  name: 'MCarousel',
  components: {
    Swiper,
    SwiperSlide,
    spaceUi
  },
  data() {
    return {
      slide_height,
      slide_ratio,
      swiperOptions: {
        slidesPerView: 'auto',
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          bulletClass: 'bullet', // 需设置.my-bullet样式
          bulletActiveClass: 'bullet-active'
        },
        autoplay: {
          delay: 3000, // 自动切换的时间间隔
          stopOnLastSlide: false, // 如果设置为true，当切换到最后一个slide时停止自动切换
          disableOnInteraction: false // 用户操作swiper之后，是否禁止autoplay。默认为true：停止
        },
        observer: true,
        observeParents: true,
        on: {
          click: function (e) {
            const inx = e.target.parentNode.getAttribute('data-item') || 0
            vm.goLink(parseInt(inx))
          }
        }
      },
      isSpace: true // 是否显示骨架屏
    }
  },
  computed: {
    ...mapState('source', ['deskNotices']),
    ...mapState('base', ['isApp'])
  },
  watch: {
    // 监听轮播数据
    deskNotices: {
      handler(list = []) {
        this.isSpace = !list.length
      },
      deep: true,
      immediate: true
    }
  },
  created() {
    vm = this
  },
  mounted() {
    this.$nextTick(() => {
      // 在 DOM 元素加载完成后进行初始化操作
      this.$refs['pageSwiperRef']?.$swiper.update()
    })
  },
  methods: {
    goLink(realIndex) {
      const item = this.deskNotices[realIndex] || {}
      jump(item, this)
    }
  }
}
</script>

<style lang="scss">
@import '@style/views/menu.carousel.modules.scss';

.m-swiper {
  height: $slide_wrapper_heihgt;
  border-radius: $swiper_radius;
  .m-swiper-slide {
    width: $slide_width;
    height: $slide_height;
    margin-right: $slide_margin_right;
    background-position: center;
    background-size: 100% 100%;
    border-radius: $swiper_radius;
    cursor: pointer;
  }
  .swiper-pagination-bullets {
    bottom: 0px;
    display: flex;
    justify-content: center;
    .bullet {
      position: relative;
      display: inline-block;
      width: $bullet_width;
      height: $bullet_height;
      background: $bullet_background;
      margin: $bullet_margin;
      border-radius: 50%;
      bottom: 0;
    }

    .bullet-active {
      background: $bullet_active_background !important;
    }
  }
}

@include breakpoint-only-xs() {
  .m-swiper {
    height: auto;
    .m-swiper-slide {
      width: 100%;
      height: auto;
    }
    .swiper-pagination-bullets {
      bottom: $xs_bullet_position_bottom !important;
      .bullet {
        background: $xs_bullet_background;
        opacity: $xs_bullet_opacity;
      }

      .bullet-active {
        background: $xs_bullet_active_background !important;
        opacity: $xs_bullet_active_opacity;
      }
    }
  }
}
</style>
